import cinemaMap from '@/components/cinemaMap.vue';
export default {
	components: {
		cinemaMap
	},
	data() {
		return {
			order: null
		};
	},
	onLoad(opt) {
		const paramsObj = JSON.parse(opt.paramsStr)
		this.initData(paramsObj)
	},
	computed: {
		navUrl() {
			if (this.order) {
				return `../cinema-detail/cinema-detail?cinemaId=${this.order.cinemaId}`
			}
		}
	},
	methods: {
		initData(order) {
			this.order = order
		}
	},
}
